/* eslint-disable react/no-multi-comp */
// Core
import React from 'react';
import useStoreon from 'storeon/react';
// import * as marked from 'marked';
import * as BlockContent from '@sanity/block-content-to-react';

import { faChevronDown } from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

// Styles
import Styles from './style.module.css';

export const ChapterVideo = ({ player }) => {
  const { dispatch, videoData, playerEvent } = useStoreon('videoData', 'playerEvent');
  const { data } = videoData;
  const { play } = playerEvent;

  const _renderChapter = () => {

    // const getMarkdownText = (text) => {
    //   const rawMarkup = marked(text, { sanitize: true, breaks: true });

    //   return { __html: rawMarkup };
    // };

    const goTo = (seconds) => {
      player.seekTo(seconds);
      !play && dispatch('event/play', true);
    };

    console.log('data.chapter', data.chapter);

    const serializers = {
      types: {
        code: (props) => (
          <pre data-language = { props.node.language }>
            <code>{props.node.code}</code>
          </pre>
        ),
      },
    };

    return (data.chapter || []).map(({ time, title, links= [], overview }, index) => (
      <li className = { Styles.chapterItem } data-time = { time } key = { index }>
        <div className = { Styles.chapterHeader }>
          <div className = { Styles.chapterOpen }>
            <FontAwesomeIcon icon = { faChevronDown } />
          </div>
          <div className = { Styles.chapterHeading } data-time = { time }>
            {title}
          </div>
        </div>
        <div className= 'collapse__content'>
          <BlockContent blocks = { overview } serializers = { serializers } />
        </div>
        {
          links.map((link) => (
            <div key = { link._key }>
              <button type = 'button' onClick = { () => goTo(link.time) }>смотреть</button>
              <a href = { link.link } rel = 'noopener noreferrer' target = '_blank'>{ link.title }</a>:
              <BlockContent blocks = { link.overview } serializers = { serializers } />
            </div>
          ))
        }
        <br />
        <div>
          <div
            className = { `button ${Styles.chapterButton}` }
            onClick = { () => goTo(time) }>
            <span className = 'button__text' >
              Перейти к разделу
            </span>
          </div>
        </div>
        <br />
      </li>
    ));
  };

  return (
    <>
      <ul className = { Styles.chapter }>{_renderChapter()}</ul>
    </>
  );
};
