import React, { useState, useEffect } from 'react';
import ReactPlayer from 'react-player';
import * as sanityClient from '@sanity/client';

import useStoreon from 'storeon/react';

// Blocks
import { Header } from './blocks/Header';
import { SideLeft } from './blocks/SideLeft';
import { SideRight } from './blocks/SideRight';
import { Footer } from './blocks/Footer';
import { Controls } from './blocks/Controls';

const urlParams = new URLSearchParams(window.location.search);
const videoId = urlParams.get('id');

const client = sanityClient({
  projectId: '4znsfupe',
  dataset:   'develop',
  // token: 'sanity-auth-token', // or leave blank to be anonymous user
  useCdn:    true, // `false` if you want to ensure fresh data
});

const videoJsOptions = {
  data: {
    title:  'Detroit: Become Human, Battlefield V, финишная прямая PlayStation 4',
    author: 'Dmitry Puchkov',
    video:  'https://www.youtube.com/watch?v=8pbuqx_Th2Y',
  },
  autoplay:        true,
  playbackRates:   [0.5, 1, 1.25, 1.5, 2],
  width:           '100%',
  height:          '100%',
  controls:        false,
  ProgressControl: true,
  controlBar:      {
    children: [
      'playToggle',
      'volumeMenuButton',
      'durationDisplay',
      'timeDivider',
      'currentTimeDisplay',
      'progressControl',
      'remainingTimeDisplay',
      'fullscreenToggle'
    ],
  },
  techOrder: ['youtube'],
  sources:   [
    {
      src:  `https://www.youtube.com/watch?v=${videoId || '8pbuqx_Th2Y'}`,
      type: 'video/youtube',
    }
  ],
};

export const VideoPlayer = () => {
  const { dispatch, videoData, playerEvent } = useStoreon(
    'videoData',
    'playerEvent',
    'playerState',
    'playerNode'
  );
  const [player, _setPlayer] = useState(null);
  const [movie, setMovie] = useState(null);
  const { play, volume, muted } = playerEvent;

  async function getMovie (query, params) {
    try {
      const _movie = await client.fetch(query, params);

      setMovie(_movie);
    } catch (error) {
      console.log('error', error);
    }
  }

  useEffect(() => {
    getMovie(`*[_type == "movie" && link == "https://youtu.be/${videoId}"] {...}[0]`, {});
  }, []);

  if (movie) {
    videoJsOptions.data.chapter = movie.content || [];
  }

  const ref = (data) => {
    _setPlayer(data);
  };

  useEffect(() => {
    dispatch('videoData/init', videoJsOptions);
  }, [dispatch, videoData]);

  const _onProgress = (updateState) => {
    if (!playerEvent.seeking) {
      dispatch('playerState/update', updateState);
    }
  };

  const _onDuration = (duration) => {
    dispatch('event/duration', duration);
  };
  // goTo = (time) => {
  //   this.player.currentTime(time);
  // }

  if (videoData === null) {
    return null;
  }
  // if (player) {

  //   console.log('player', player.getInternalPlayer());
  // }
  const { data, width, height } = videoData;

  return (
    <div>
      <div data-vjs-player>
        <div className = 'player'>
          <Header data = { data } />
          <div className = 'player__main'>
            <SideLeft player = { player } />
            <div className = 'player__frame'>
              <div className = 'player__content'>
                <ReactPlayer
                  config = { {
                    youtube: {
                      playerVars: {
                        controls: 0,
                        showinfo: 1,
                      },
                    },
                  } }
                  height = { height }
                  muted = { muted }
                  playing = { play }
                  ref = { ref }
                  url = { data.video }
                  volume = { volume }
                  width = { width }
                  onDuration = { _onDuration }
                  onProgress = { _onProgress }
                />
              </div>
            </div>
            <SideRight />
          </div>
          <Controls player = { player } />
          <Footer />
        </div>
      </div>
    </div>
  );
};
